@keyframes sheen {
  100% {
    transform: translateX(100%);
  }
}
#modal-success {
  background: #ffffff;
  border-radius: 20px;
  display: none;
}
@media (max-width: 576px) {
  #modal-success {
    border-radius: 16px;
  }
}
#modal-success .is-close {
  opacity: 0.5 !important;
  transition: all 0.4s !important;
  top: 5px !important;
  right: 10px !important;
}
@media (max-width: 576px) {
  #modal-success .is-close {
    top: 0px !important;
    right: 0px !important;
  }
}
#modal-success .is-close:hover {
  opacity: 0.8 !important;
}
#modal-success h3 {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 45px;
  line-height: 115%;
  color: #2b2b2b;
  text-align: center;
  font-size: calc(0.011875 * 100vw + 22.2px);
}
#modal-success h3 br {
  display: none;
}
@media (min-width: 1921px) {
  #modal-success h3 {
    font-size: 45px;
  }
}
@media (max-width: 320px) {
  #modal-success h3 {
    font-size: 26px;
  }
}
@media (max-width: 576px) {
  #modal-success h3 br {
    display: block;
  }
}

#modal-success {
  max-width: 810px;
  width: 100%;
  padding: 50px 40px 50px;
}
@media (max-width: 576px) {
  #modal-success {
    padding: 35px 10px 25px;
  }
}
#modal-success .decr {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 25px;
  text-align: center;
  color: #707070;
  margin-top: 38px;
}
@media (max-width: 576px) {
  #modal-success .decr {
    margin-top: 13px;
    font-size: 12px;
    line-height: 14px;
  }
}
#modal-success .decr span {
  font-weight: 700;
}